import {FC} from 'react';
import ReactApexChart from 'react-apexcharts';


export const RegionenChart : FC  = () =>{
    const chartData  = {
          
        series: [8, 32, 56, 4],
        labels: ["Dach", "Ex Dach" , "USA" , "Sonstige"],
        colors:['#003F7A','#185287','#2f6393','#43729e'],
        options: {
          chart: {
            type: 'donut',
          },
          legend: {
            show: true,
            position: "bottom",
            fontSize: '24px',
          },
        },
    }
      
    return(
        <>
          <ReactApexChart width={400} options={chartData} series={chartData.series} type="donut" />
        </>
    )
}