// Dependencies
import { FC } from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';

// Routes
import { MAIN_ROUTES, PageType, ROUTES } from '../contants';

interface IAppRouterProps {
  page: PageType
}

// Create app router
const AppRouter: FC<IAppRouterProps> = ({ page }) => (
  <Router>
    <Routes>
      {MAIN_ROUTES.map(({ path, component: C }) => (
        <Route key={path} path={path} element={<C />} />
      ))}
      <Route
        path={'*'}
        key={''}
        element={<Navigate to={MAIN_ROUTES.find((route) => route.page === page)?.path || ''} />}
      />
    </Routes>
  </Router>
);

// Export app router
export default AppRouter;
