// Dependencies
import React, {FC, useEffect} from 'react';
import { onError } from '@apollo/client/link/error';
import { ApolloClient, ApolloProvider, from, HttpLink, InMemoryCache } from '@apollo/client';
import { ThemeProvider } from '@mui/material';

// Router
import AppRouter from './router';

// Theme
import theme from './theme';

// Constants
import { PageType, SERVER_API } from './contants';

// Prepare links
const errorLink = onError((errors: any) => {
  if (errors.graphqlErrors) {
    errors.graphqlErrors.map((data: any) => {
      alert(`Graphql error ${data.message}`);
    });
  }
});

const apiKey = localStorage.getItem('apiKey');

const link = from([errorLink, new HttpLink({ uri: SERVER_API, headers: { apiKey } })]);

// Create client
export const client = new ApolloClient({
  cache: new InMemoryCache(),
  link: link,
});

interface IAppProps {
  apiKey: string
  page: PageType
}

// Create app
const App: FC<IAppProps> = ({ apiKey, page }) => {
  useEffect(() => {
    localStorage.setItem('apiKey', apiKey);
  }, [apiKey]);

  return (
    <ApolloProvider client={client}>
      <ThemeProvider theme={theme}>
        <AppRouter page={page} />
      </ThemeProvider>
    </ApolloProvider>
  )
};

// Export app
export default App;
