// Dependencies
import { FC } from 'react';

// Pages
import { FactSheetPage, PerformancePage, ValuePage } from '../pages';

// Interfaces
interface IRoute {
  page: PageType;
  path: string;
  component: FC;
}

// Export routes
export const ROUTES = {
  PRODUCT: {
    PREFIX: '/product',
    INDEX: '/product',
    CHART: {
      PREFIX: '/product/chart',
      INDEX: '/product/chart/performance',
      PERFORMANCE: '/product/chart/performance',
      VALUE: 'product/chart/value',
    },
    FACT_SHEET: '/product/LU2439874319/de/:name',
  },
};

// Page
export type PageType = 'performance' | 'value' | 'factsheet'

// Export main routes
export const MAIN_ROUTES: IRoute[] = [
  {
    page: 'performance',
    path: ROUTES.PRODUCT.CHART.PERFORMANCE,
    component: PerformancePage,
  },
  {
    page: 'value',
    path: ROUTES.PRODUCT.CHART.VALUE,
    component: ValuePage,
  },
  {
    page: 'factsheet',
    path: ROUTES.PRODUCT.FACT_SHEET,
    component: FactSheetPage,
  },
];
