import {FC} from 'react';
import ReactApexChart from 'react-apexcharts';


export const BranchenChart : FC  = () =>{
    const chartData  = {
          
        series: [28, 40, 8, 20 , 4],
        labels: ["Kommunikation", "Konsumgter" , "Finanzwesen" , "Technologie","Sonstige"],
        colors:['#003F7A','#185287','#2f6393','#43729e','#43729e'],
        options: {
          chart: {
            type: 'donut',
          },
          legend: {
            show: true,
            position: "top",
            containerMargin: {
              top: 0
            }
          },
          responsive: [
            {
              breakpoint: 500,
              options: {
                legend: {
                  fontSize: "11px"
                }
              }
            }
          ]
        },
    }
      
    return(
        <>
          <ReactApexChart width={400} options={chartData} series={chartData.series} type="donut" />
        </>
    )
}