import {FC} from 'react';
import ReactApexChart from 'react-apexcharts';


export const MarktkapitalisierungChart : FC  = () =>{
    const chartData  = {
        series: [0, 8, 92],
        labels: ["Small Cap", "Mid Cap" , "Large Cap"],
        colors:['#003F7A','#185287','#2f6393'],
        options: {
          chart: {
            type: 'donut',
          },
          legend: {
            show: true,
            position: "top",
            containerMargin: {
              top: 0
            }
          },
          responsive: [
            {
              breakpoint: 500,
              options: {
                legend: {
                  fontSize: "11px"
                }
              }
            }
          ]
        },
    }
      
    return(
        <>
          <ReactApexChart width={400} options={chartData} series={chartData.series} type="donut" />
        </>
    )
}